html, body {
	margin: 0;
	padding: 0;
	font-family: 'MetricWeb';
	height: 100%;
}

div, form {
	box-sizing: content-box;
}

#tech-radar__form-container {

	transition: height 0.6s ease;
	overflow: hidden;
	position: relative;

	&.collapsed {
    	height: 7em !important;
		&:after {
			content: '';
			pointer-events: none;
			background: linear-gradient(to bottom, rgba(246,233,216,0) 0%,rgba(246,233,216,1) 100%);
			position: absolute;
			left: 0;
			bottom: 0;
			right: 0;
			height: 1em;
		}
	}

}

#tech-radar__qp-form {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;

	& > div {
		flex-grow: 1;
	}
}

.filter-table {

	white-space: nowrap;

	thead {
		background: #ccc;
	}

	tr.collapsed {

		td.hidden {
			.datum {
				display: none;
			}
			&:after {
				content: 'Click To Display';
			}
		}
	}
}

svg {
	max-width: 100%;
	height: auto;

	.d3-label {
		fill: #333;
		fill-opacity: 1;
		z-index: 10;
		font-size: 14pt;
		font-weight: bold;
		pointer-events: none;
		font-family: MetricWeb, helvetica, sans;
		transition: transform 0.2s ease;
		text-anchor: end;

		&.bg {
			stroke: white;
			stroke-width: 3px;
			stroke-linecap: round;
			stroke-linejoin: round;
			stroke-opacity: 1;
			stroke-alignment: outer;
		}

		&.no-dot {
			fill: black;
			text-decoration: underline;
			font-size: 18pt;
		}
	}

	circle.background {
		pointer-events: none;
	}

	circle.node {
		fill: white;
		stroke-width: 2px;
		stroke: black;

		&.no-dot {
			display: none;
			font-size: 16pt;
		}
	}
}

.hovering {
	font-weight: bold;

	.d3-label {
		font-size: 150%;
	}
}

.details {
	padding-left: 0;

	li {
		list-style: none;
		width: 10em;
		margin-bottom: 0.5em;
		white-space: normal;
		font-weight: normal !important;

		.key {
			font-weight: bold;
		}
	}
}

#error-text-target {
	white-space: pre;

	&:empty {
		display: none;
	}
}
